.card-list{
    width:85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap:20px;
}
.show-card{
    background-color: cornsilk;
    border-radius: 10px;
    padding: 10px;
    color: blueviolet;
}
.search-field{
    border: 0;
    background-color: cornflowerblue;
    border-bottom: 1px solid blueviolet;
    margin:0px auto;
    color: white;
    width: 30%;
}